.li {
  position: relative;
  border-radius: 20px;
  max-width: 400px;
  height: fit-content;
}

.image {
  border-radius: 30px;

  width: 400px;
  height: 240px;
  object-fit: cover;
}

.likeButton {
  background: url(../../images/like-button.svg) no-repeat center center;
  width: 40px;
  height: 40px;
  top: 20px;
  right: 20px;
  border: none;
  cursor: pointer;
  z-index: 10;
}

.buttonContainer {
  border-radius: 50%;
  background-color: gray;
  width: 40px;
  height: 40px;
  position: absolute;
  z-index: 5;
  top: 20px;
  right: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.title {
  font-weight: 700;
  font-size: 20px;
  line-height: 26px;
}

.location {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #878787;
}

.date {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #878787;
}

.price {
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
}

.container {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding-top: 18px;
}
