.section {
  width: 100%;
  padding: 80px 0 175px;
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  background: #c4c4c4;
}

.container {
  gap: 18px;
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  margin-bottom: 40px;
}

.headText {
  font-weight: 700;
  font-size: 46px;
  line-height: 56px;
  letter-spacing: -0.32px;
  text-align: center;
  margin: 0;
  padding: 0;
}

.subText {
  font-weight: 400;
  font-size: 20px;
  line-height: 32px;
  letter-spacing: 0.08px;
  text-align: center;
}
