.section {
  width: 100%;
  max-width: 1232px;
  display: flex;
  flex-direction: column;
  padding-top: 62px;
}

.title {
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
}

.list {
  display: flex;
  gap: 16px;
  width: 100%;
  height: fit-content;
  list-style-type: none;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 38px;
}
