.form {
  max-width: 608px;
  width: 100%;
  height: 56px;
  background: #fff;
  border-radius: 34px;
  display: flex;
  padding: 19px 0;
  border: 1px solid #f0ecec;
}

.form:hover {
  outline: 1px solid #06b6d4;
}

.fieldset {
  width: 100%;
  display: flex;
  align-items: center;
  outline: none;
  border: none;
  gap: 18px;
  margin-left: 22px;
}

.icon {
  width: 18px;
  height: 18px;
  object-fit: contain;
}

.input {
  width: 100%;
  outline: none;
  border: none;
}

.input::placeholder {
  font-weight: 700;
  font-size: 15px;
  line-height: 18px;
  letter-spacing: 0.2px;
}
